import React from 'react';
import { NavLink } from 'react-router-dom';

import './Menu.css';

class Menu extends React.Component {
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		console.log(this.props.match);
	}

	render() {
		console.log(this.props.match);
		return (
			<div>            
				<div className='Menu no-scroll'>
					<div className='Menu-logo-base'>
						<NavLink to="/" className='Menu-link'>
							<div className='Menu-logo'>ConnectG</div>
						</NavLink>
					</div>
					<ul className="Menu-main">
						<li>
							<NavLink to="/about" className="Menu-title Menu-link">   </NavLink>
						</li>
						<li>
							<NavLink to="/management" className="Menu-title Menu-link">    </NavLink>
							<ul className="Menu-child">
								<li>
									<NavLink to="/management/contract" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/management/language" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/management/injury" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/management/promote" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/management/feedback" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
							</ul>
						</li>
						<li>
							<NavLink to="/contact" className="Menu-title Menu-link">   </NavLink>
							<ul className="Menu-child">
								<li>
									<NavLink to="/contact/visit" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/contact/call" className="Menu-subtitle Menu-link"> </NavLink>
								</li>
								<li>
									<NavLink to="/contact/discord" className="Menu-subtitle Menu-link">   </NavLink>
								</li>
							</ul>
						</li>
						<li>
							<NavLink to="/partnership" className="Menu-title Menu-link">  </NavLink>
						</li>
						<li>
							<div className="Menu-footer">
								<div>
									<img className="Menu-footer-img" src={require('./call.png')}/>
									<span>02)0000-0000</span>
								</div>
								<div>								
									<img className="Menu-footer-img" src={require('./Discord-Logo-Color.png')}/>
									<span>https://discord.gg/XXXXXX</span>
								</div>
							</div>
						</li>
					</ul>
				</div>
			</div>
		);
	}
};

export default Menu;


